import React, {Suspense, useEffect, useState} from 'react';
import logo from './logo.svg';
import './App.css';
import pepeSad from './img/pepeSad.jpg';
import pepeHappy from './img/pepeHappy.jpg';
import { Button, Grid, TextField } from '@mui/material';
import axios from 'axios';
import Confetti from 'react-confetti';

const playMusic = () => {
  var music = new Audio('/poseidon.mp3');
  music.play();
}



function App() {
  const [serverMessage, setServerMessage] = useState<string>("");
  const [pass, setPass] = useState<string>("");
  const [button, setButton] = useState<string>("Reiniciar");
  const [colorButton, setColorButton] = useState<"primary" | "error" | "success">("success");
  const [forceDisableButton, setForceDisableButton] = useState<boolean>(false);
  const [pepeSadState, setPepeSadState] = useState<boolean>(true);
  const [wh, setWH] = useState<[number, number]>([window.innerWidth, window.innerHeight]);
  const [net, setNet] = useState<boolean>(false);
  const changePass = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) =>{
    setPass(e.currentTarget.value);
  }

  useEffect(() => {
    const handleResize = () => {
      setWH([window.innerWidth, window.innerHeight]);
    };
    window.addEventListener('resize', handleResize);
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  const postCall = () => {
    if (net) return;
    setNet(true);
    setColorButton("primary");
    setButton("Hold");
    axios.post('/restart', {
      msg: pass
    })
    .then(function (response) {
      let code = response.status;
      let msg = response.data.msg;
      setPepeSadState(false);
      setServerMessage(msg);
      setColorButton("success");
      setButton("Listo");
      setForceDisableButton(true);
      playMusic();
    })
    .catch(function (error) {
      //let code = error.response.status;
      let msg = error.response.data.msg;
      console.log(msg);
      setServerMessage(msg);
      setButton("Reiniciar");
      setColorButton("error");
    }).finally(function(){
      setNet(false)
    }

    );
  }

  const confetti =  () => {
    return (
      <Confetti
        width={wh[0]}
        height={wh[1]}
      />
    )
  }

  return (
    <div className="App" style={{width: '100%', textAlign: 'center'}}>
      {(!pepeSadState)? confetti(): ""}
      <Grid container style={{width: '300px', margin: 'auto', padding: '10px'}}>
        <Grid item xs={6}>
          <img style={{width:'100px'}} src={(pepeSadState)?pepeSad:pepeHappy}></img>
        </Grid>
        <Grid item xs={6}>
          <Grid container>
            <Grid item xs={12} >
              <TextField disabled={forceDisableButton} id="pass" label="Password" variant="outlined" type="password" size='small' margin='dense' value={pass} onChange={(event)=>changePass(event)}/>
            </Grid>
            <Grid item xs={12}>
              <Button 
                variant='contained' 
                color={colorButton}
                disabled={(pass=="" || forceDisableButton)? true:false} 
                onClick={postCall}>
                  {button}
              </Button>
            </Grid>
          </Grid>
        </Grid>
        <Grid item xs={12}>
          <p>{serverMessage}</p>
        </Grid>
      </Grid>
       
    </div>
  );
}

export default App;


